import React from "react";
import styled from "styled-components";
import AppFooter from "src/components/common/AppFooter";
import AppHeader from "src/components/common/AppHeader";

const AppLayoutContainer = ({ children, noPadding = false }) => {
  return (
    <>
      <AppHeader />
      <StyledPageLayout noPadding={noPadding}>{children}</StyledPageLayout>
      <AppFooter />
    </>
  );
};

export default AppLayoutContainer;

const StyledPageLayout = styled.div`
  width: 100%;
  padding: ${props => (props.noPadding ? "0" : "0 2rem")};
  min-height: calc(100vh - 13rem - 10vh);
`;
