import React from "react";
import palette from "src/lib/styles/palette";
import styled from "styled-components";
import arrange from "src/lib/styles/arrage";
import CustomIcon from "src/lib/CustomIcon";

const SkillCard = ({ cardContent }) => {
  return (
    <>
      <FlipCard>
        <FrontCard>
          <CustomIcon size="large" font>
            <img
              src={cardContent.frontImg}
              alt="frontImg"
              width="50px"
              height="50px"
            ></img>
            <div>{cardContent.frontContent}</div>
          </CustomIcon>
        </FrontCard>
        <BackCard numberOfContent={cardContent.backContent.length}>
          {cardContent.backContent.map((content, i) => (
            <BackCardContent key={i}>{content}</BackCardContent>
          ))}
        </BackCard>
      </FlipCard>
    </>
  );
};

export default SkillCard;

const FlipCard = styled.div`
perspective: 150rem;
position: relative;
width: 13rem;
height: 17rem;
  text-align: center;
  & > div {
    padding: 2rem 1rem;
    width:100%;
    height:100%;
    position:absolute;
    top: 0;
    left : 0;
    color : black;
    font-size: 1.4rem;
    backface-visibility:hidden;
  }
  & > div:last-child {
    background : ${palette.gray3}
    transform : rotateY(180deg);
  }
  & > div:first-child{
    ${arrange.center}
    background : ${palette.gray7}
  }
  &:hover {
    & > div:last-child {
      transition : all .8s
      transform : rotateY(0deg);
    } 
    & > div:first-child {
      transition : all .8s
      transform : rotateY(180deg);
    } 
  }
`;

const FrontCard = styled.div``;
const BackCard = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
`;

const BackCardContent = styled.div`
  width: 100%;
  height: 2rem;
  font-size: 0.9rem;
  border-radius: 5px;
  background: yellow;
  padding-top: 0.3rem;
`;
